        <h3>YALA NATIONAL PARK</h3>
            Yala is a national park combined with a stringent nature reserve. The park, which is 
            divided into 5 blocks, protects an area of territory covering close to 130,000 hectares and 
            is made up of light woods, scrubs, grasslands, tanks, and lagoons. The public may 
